/***
	Basic Validation Func
	***/
function validateField( f ) {
//	console.log( typeof f.validate )
	if( typeof f.validate != 'undefined' ) {
		//console.log(f.validate)
		//Required fields checks if value is not empty
		if( f.validate.required ) {
			if( f.value.length == 0 ) {
				return {
					error: f.validate.required.error,
				}
			}
		}
/*
		else {
			if( f.value.length == 0 ) {
				return true
			}
		}
*/
		//Pattern
		if( f.validate.pattern ) {
			if( !f.value.match( f.validate.pattern.reg ) ) {
				return {
					error: f.validate.pattern.error,
				}
			}
		}
		//Custom callback for validation
		if( f.validate.callback ) {

			return f.validate.callback( f.value )
		}

		return true;
	}
	else {
		return true;
	}
}
/***
	Form validation func
	***/
function validateForm( e ) {
	var f = e.data
	//console.log(f)
	e.preventDefault()
	var errors = []
	for( var k in f.fields ) {

		if(  f.fields.hasOwnProperty( k )  ) {
			var $field = $(this).find( '[data-field="' + k + '"]' )
		//	console.log(k, $(this) )
			var v = $field.val()
			f.fields[k].value = v
		//	console.log(f)
			var result = validateField(f.fields[k])
			//console.log(result)
			if( result !== true ) {
				if( typeof f.fields[k].onError == 'function' ) {
					f.fields[k].onSubmitError( $field, result )
				}
				else {
					onSubmitError( $field, result )
				}
				errors.push( result )
			}
			else {
				if( typeof f.fields[k].onSuccess == 'function' ) {
					f.fields[k].onSuccess( $field )
				}
				else {
					onSuccess( $field )
				}
			}
		}
	} //endfor
	//
	//
	if( errors.length == 0 ) {
		var $form = $( this )
		var form = forms.filter( function( form ) {
			//console.log( form.id, $form )
			return form.id == $form.attr('id')
		} )
		if( form.length == 0 ) {
			console.log( 'No form set' )
			onFormSuccess( $(this) )
		}
		else {
			//
			if( typeof form[0].onFormSuccess == 'function' ) {
				form[0].onFormSuccess( $(this) )
			}
			else {
					onFormSuccess( $(this) )
			}
			//
		}
	}
}

function validateSingleField( e ) {
	//console.log(1)
	var field = e.data
	//console.log(field)
	var $field = $( this )
	field.value = $field.val()
	var result = validateField( field )
	//console.log(result)
	if( result !== true ) {
		if( typeof field.onError == 'function' ) {
			field.onFieldError($field, result )
		}
		else {
			onFieldError($field, result )
		}
	}
	else {
		if( typeof field.onSuccess == 'function' ) {
			field.onSuccess( $field )
		}
		else {
			onSuccess( $field )
		}
	}
}
// field - input dom el, being validated
function onFieldError( field, result ) {
	$( field ).parent().addClass('has-error')
	$( field ).parent().removeClass('has-success')
}

function onSubmitError( field, result ) {
	$( field ).parent().addClass('has-error')
	$( field ).parent().removeClass('has-success')
	if( $( field ).parent().find('.error-text').length == 0 ) {
		$( field ).parent().append( '<span class="error-text">'+ result.error +'</span>' )
	}
}

function onSuccess( field ) {
	$( field ).parent().removeClass('has-error')
	$( field ).parent().addClass('has-success')
	$( field ).parent().find('.error-text').remove()
}

function onFormSuccess( form ) {
	console.log(form, 'success')
}

var phoneFormSuccess = function( form ) {
	var data = form.serialize()
	//analytics
	//data += '&s=' + location.search
	data += '&r=' + document.referrer
	form.addClass('is-loading');
	form.find('button').attr('disabled', 'disabled')
	//console.log(data)

	$.ajax({
		url: '/backend/send',
		method: 'POST',
		data: data,
		context: form,
		success: function( response ) {
			var form = $(this)

			//dataLayer.push({'event': form.attr('id'), 'eventCategory' : 'form', 'eventAction' : 'sent'}); **deprecated
			dataLayer.push({
                'event' : 'formsend',
                'eventCategory' : 'form',
                'eventAction' : 'send-btn',
                'eventLabel' : form.attr('id'),
            });

			//console.log( form.attr('id'), response )
			console.log( response )
			setTimeout( function() {
				form.removeClass('is-loading')
				form.addClass('is-success')
				form.append( '<span class="message message-success">'+response+'</span>' )
			}, 2000 )
		},
		error: function( response ) {
			console.log( response )

			setTimeout( function() {
				form.removeClass('is-loading')
				form.addClass('is-error')
				if( response.responseText && response.responseText.length > 0 ) {
					form.append( '<span class="message message-error"> '+response.responseText+'</span>' )
				}
				else {
					form.append( '<span class="message message-error"> '+response.status+' '+response.statusText+'</span>' )
				}

			}, 2000 )
		},

	})
}

var detailedFormSuccess = function( form ) {
	console.log( form[0] )
	var data = new FormData( form[0] )
	//analytics
	//data.append( 's', location.search )
	data.append( 'r', document.referrer )

	form.addClass('is-loading');
	form.find('button').attr('disabled', 'disabled')

	$.ajax({
		url: '/backend/detailed',
		method: 'POST',
		data: data,
		context: form,
		processData: false,
  	contentType: false,
		success: function( response ) {
			var form = $(this)
			console.log( form.attr('id'), response )
			//dataLayer.push({'event': form.attr('id'), 'eventCategory' : 'form', 'eventAction' : 'sent'}); **deprecated
			dataLayer.push({
                'event' : 'formsend',
                'eventCategory' : 'form',
                'eventAction' : 'send-btn',
                'eventLabel' : form.attr('id'),
            });
			setTimeout( function() {
				form.removeClass('is-loading')
				form.addClass('is-success')
				form.append( '<span class="message message-success">'+response+'</span>' )
			}, 2000 )
		},
		error: function( response ) {
			console.log( $(this), response )
			$(this).removeClass('is-loading')
			form.addClass('is-error')
			if( response.responseText && response.responseText.length > 0 ) {
				form.append( '<span class="message message-error"> '+response.responseText+'</span>' )
			}
			else {
				form.append( '<span class="message message-error"> '+response.status+' '+response.statusText+'</span>' )
			}
		},
	})
}

//start fields
var fields = {
	phone: {
		value: '',
		validate: {
			required: {
				error: '  ',
			},
			pattern: {
				reg: /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/,
				error: '     ',
			}
		},
	},
	name: {
		value:'',
		validate: {
			required: {
				error: '  ',
			},
			pattern: {
				reg: /[--\s]+/,
				error: '     ',
			}
		}
	},
	description: {
		value: '',
		validate: {
			required: {
				error: '  ',
			},
			callback: function( v ) {
				if( v.length > 10 && v.length < 200 ) {
					return true
				}
				else {
					return {error: '     10   200 '}
				}
			}
		},
	},
	model: {
		value: '',
		validate: {
			required: {
				error: ', ,  ',
			},
			callback: function( v ) {
				if( v.length > 4 && v.length < 50 ) {
					return true
				}
				else {
					return {error: '      4   50 '}
				}
			}
		},
	},
	notebook: {
		value: '',
		validate: {
			required: {
				error: ', ,  '
			}
		}
	},
	state: {
		value: '',
		validate: {
			required: {
				error: ', ,  '
			}
		}
	},
	photo: {
		value: '',
		validate: {
			required: {
				error: ', ,  '
			}
		}
	},
}
//end fields

var forms = [
	{
		id: 's1-form',
		fields: {
			phone: fields.phone,
		},
		onFormSuccess: phoneFormSuccess,
	},
	{
		id: 'modal-form-2',
		fields: {
			phone: fields.phone,
		},
		onFormSuccess: phoneFormSuccess,
	},
	{
		id: 'prefooter-form',
		fields: {
			phone: fields.phone,
		},
		onFormSuccess: phoneFormSuccess,
	},
	{
		id: 'modal-form',
		fields: {
			name: fields.name,
			phone: fields.phone,
			description: fields.description,
			notebook: fields.notebook,
			photo: fields.photo,
			state: fields.state,
			model: fields.model
		},
		onFormSuccess: detailedFormSuccess,
	},
]
//end forms

//Handlers
for( var i=0; i<forms.length; i++ ) {
	$('#'+forms[i].id).on('submit', forms[i], validateForm );
	for( var k in forms[i].fields ) {
		//console.log($( '#'+forms[i].id ).find( '[data-field="' + k + '"]' ))
		$( '#'+forms[i].id ).find( '[data-field="' + k + '"]' ).on('keyup', forms[i].fields[k], validateSingleField )
		$( '#'+forms[i].id ).find( '[data-field="' + k + '"]' ).on('change', forms[i].fields[k], validateSingleField )
	}
}

//Error text closd on click
$(document).on( 'click', '.error-text', function( e ) {
	$(this).remove()
} )

//Prevent dummies from 8 in phone number error
$('[data-field="phone"]').on( 'keypress', function(e){
    $(this).val( $(this).val().replace( /^\+7 \(8/, '+7 (' ) )
} )
